* {
  box-sizing: border-box;
}

body {
  background: #fbcc1433;
  margin: 0;
  font-family: Lato, Arial, sans-serif;
}

.wrapper {
  max-width: 800px;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  padding: 10px;
}

figure {
  width: 250px;
  margin: 20px auto 0;
}

.logo-wrap {
  height: 0;
  padding-bottom: 45.2%;
}

figure img {
  width: 100%;
  display: block;
}

h1 {
  margin: 0;
  font-family: Lora;
  font-size: 1.875rem;
  line-height: 2.125rem;
}

h2 {
  margin-bottom: 5px;
  font-family: Lora;
  font-size: 1.25rem;
  line-height: 1.375rem;
}

span {
  text-align: center;
  margin-top: .625rem;
  font-family: Lora;
  font-size: 1rem;
  font-style: italic;
  font-weight: 400;
  line-height: 1.125rem;
  display: block;
}

p {
  margin: 0;
  line-height: 1.4;
}

textarea {
  -webkit-appearance: none;
  color: #000000de;
  min-height: 200px;
  resize: vertical;
  width: 100%;
  background: #fff;
  border: 1px solid #22242626;
  border-radius: 4px;
  margin: 0 0 10px;
  padding: .785714em 1em;
  font-family: Lato;
  font-size: 1em;
  line-height: 1.2857;
  transition: color .1s, border-color .1s;
}

textarea:focus {
  color: #000000f2;
  border-color: #85b7d9;
}

button {
  cursor: pointer;
  font-family: Lato, sans-serif;
}

.form-header {
  position: relative;
}

.form-header button {
  color: #4a4a4a;
  background: none;
  border: 0;
  font-size: 12px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.haags-resultaat {
  position: relative;
}

.haags-resultaat button {
  color: #4a4a4a;
  background: none;
  border: 0;
  padding: 20px 5px 5px 15px;
  font-size: 12px;
  position: absolute;
  bottom: 15px;
  right: 0;
}

.button {
  vertical-align: baseline;
  color: #fff;
  text-align: center;
  background: #0d7e5d;
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 0 0 20px;
  padding: 0 20px;
  font-size: 1rem;
  font-style: normal;
  line-height: 2.75rem;
  transition: all .1s;
  display: inline-block;
}

.button:hover {
  background-color: #229c79;
}

a {
  color: #3e3e3e;
  text-decoration: none;
}

nav a, p a {
  border-bottom: 1px solid;
}

nav a:hover, p a:hover {
  border: none;
}

.totstandkoming {
  text-align: left;
  border: 1px solid #000;
  border-radius: 4px;
  margin-top: 100px;
  padding: 20px;
}

.totstandkoming h1 {
  margin-bottom: 0;
  font-size: 1.125rem;
  line-height: 1.4;
}

.totstandkoming p + p {
  margin-top: 22px;
}

nav {
  margin-top: 40px;
}

footer {
  margin-top: 30px;
  margin-bottom: 30px;
}

footer p {
  margin-top: 10px;
  font-size: .875rem;
  line-height: 1rem;
}

footer img {
  width: 40px;
}

